import React from "react";
import SmallHeading from "../UI/SmallHeading";
import MediumHeading from "../UI/MediumHeading";
import Button from "../UI/Button";
import Tile from "../UI/Tile";
import me from "../../assets/images/azeem-left.png";

import "./style.css";

/**
 * @author
 * @function Qualification
 **/

const Qualification = (props) => {
  return (
    <div className="container" style={{ padding: "50px 0" }}>
      <SmallHeading text="Qualifications" />
      <MediumHeading text={"My Education"} />
      <div
        className="flexRow flexCol align-center justify-sb"
        style={{ margin: "50px 0" }}
      >
        <div data-aos="fade-up-right">
          <img src={me} alt="" />
          <div
            style={{
              background: "#fff",
              padding: "10px 10px",
              boxSizing: "border-box",
              display: "flex",
              justifyContent: "center",
              borderRadius: "20px",
            }}
          >
            <div className="mlr-10">
              <Button label="Hire Me" />
            </div>
            <div className="mlr-10">
              <Button label="Download CV" inverse={true} />
            </div>
          </div>
        </div>
        <div>
          <Tile
            title="Esoft Metro Campus"
            mediumTitle="Pearson BTEC Level 5 HND (Higher National Diploma) in Computing."
            desc="(2020)"
          />
          <Tile
            title="STYAVA.DEV"
            mediumTitle="Successfully Completed Azure Administrator (AZ 104) Certification."
            desc="(2022)"
          />
          <Tile
            title="University Of Morattuve"
            mediumTitle="Following Online Fullstack Software Developer Course."
            desc="(2022)"
          />
        </div>
      </div>
    </div>
  );
};

export default Qualification;
