import React from "react";
import socialIcons from "../../../assets/social-icons";
import "./style.css";

/**
 * @author
 * @function SocialConnect
 **/

const SocialConnect = (props) => {
  return (
    <div className="socialConnect" style={props.style}>
      <span className="textColor font-12">Follow me on: </span>
      <a className="socialLink" href="https://www.facebook.com/jamaldeenazeem.azeem">
        <img src={socialIcons.facebook} alt="" />
      </a>
      <a className="socialLink" href="https://www.youtube.com/channel/UCjx6smhUkJxZzYgO5g59_zw">
        <img src={socialIcons.youtube} alt="" />
      </a>
      <a className="socialLink" href="https://www.linkedin.com/in/mohamed-azeem-107304220/">
        <img src={socialIcons.linkedin} alt="" />
      </a>
    </div>
  );
};

export default SocialConnect;
